<template>
    <div class="integral-shop">
        <div class="banner">
            <el-carousel
                    :interval="5000"
                    height="180px"
                    arrow="always"
                    v-if="tableData.hasOwnProperty('banner') && tableData.banner.length>0">
                <el-carousel-item v-for="item in tableData.banner" :key="item.id">
                    <div class="banner-img">
                        <a class="img" :href="item.url ? item.url : ''" :style="{ background:'url('+item.img+') no-repeat center' }" :target="'_'+ item.blank ? item.blank : ''">
                        </a>
                    </div>
                </el-carousel-item>
            </el-carousel>
            <div class="banner-bottom">
                <router-link :to="{name:'integralPoint'}" class="item">
                    <i class="bbx-font bbx-icon-jifenshangcheng"></i>
                    <span class="span">{{ $L('积分') }}：{{ tableData.pay_points }}</span>
                </router-link>
                <router-link :to="{name:'exchangeRecord'}" class="item">
                    <i class="bbx-font bbx-icon-drawdown"></i>
                    <span class="span">{{ $L('兑换记录') }}</span>
                </router-link>
                <router-link :to="{name:'integralTasks'}" class="item">
                    <i class="icon bbx-font bbx-icon-gouwuche2"></i>
                    <span class="span">{{ $L('赚积分') }}</span>
                </router-link>
            </div>
        </div>

        <div class="title-all">
            <section-title class="left" v-bind:tit="$L('当前积分可兑')"></section-title>
            <router-link :to="{name:'integralList',params:{type:'all', points:'all'}}"><div class="right mainFontColor">{{ $L('全部') }}>></div></router-link>
        </div>
        <div v-if="tableData.hasOwnProperty('convertible') && tableData.convertible.length>0">
            <div class="content-item">
                <ul class="content-ul">
                    <li class="content-list" v-for="(item,index) in tableData.convertible" v-bind:key="index">
                        <integral-style-one v-bind:data="item"></integral-style-one>
                    </li>
                </ul>
            </div>
        </div>
        <div class="tips" v-else>
            {{ $L('暂无数据') }}
        </div>
        <div class="title-all">
            <section-title class="left" v-bind:tit="$L('优惠券兑换')"></section-title>
            <router-link :to="{name:'integralList',params:{type:'coupon', points:'all'}}"><div class="right mainFontColor">{{ $L('全部') }}>></div></router-link>
        </div>
        <div v-if="tableData.hasOwnProperty('coupon') && tableData.coupon.length>0">
            <div class="content-item">
                <ul class="content-ul">
                    <li class="content-list" v-for="(item,index) in tableData.coupon" v-bind:key="index">
                        <integral-style-one v-bind:data="item"></integral-style-one>
                    </li>
                </ul>
            </div>
        </div>
        <div class="tips" v-else>
            {{ $L('暂无数据') }}
        </div>
        <div class="title-all">
            <section-title class="left" v-bind:tit="$L('产品兑换')"></section-title>
            <router-link :to="{name:'integralList',params:{type:'product', points:'all'}}"><div class="right mainFontColor">{{ $L('全部') }}>></div></router-link>
        </div>
        <div v-if="tableData.hasOwnProperty('product') && tableData.product.length>0">
            <div class="content-item">
                <ul class="content-ul">
                    <li class="content-list" v-for="(item,index) in tableData.product" v-bind:key="index">
                        <integral-style-one v-bind:data="item"></integral-style-one>
                    </li>
                </ul>
            </div>
        </div>
        <div class="tips" v-else>
            {{ $L('暂无数据') }}
        </div>
        <div class="title-all">
            <section-title class="left" v-bind:tit="$L('礼品兑换')"></section-title>
            <router-link :to="{name:'integralList',params:{type:'gift', points:'all'}}"><div class="right mainFontColor">{{ $L('全部') }}>></div></router-link>
        </div>
        <div v-if="tableData.hasOwnProperty('gift') && tableData.gift.length>0">
            <div class="content-item">
                <ul class="content-ul">
                    <li class="content-list" v-for="(item,index) in tableData.gift" v-bind:key="index">
                        <integral-style-one v-bind:data="item"></integral-style-one>
                    </li>
                </ul>
            </div>
        </div>
        <div class="tips" v-else>
            {{ $L('暂无数据') }}
        </div>
    </div>
</template>

<script>
    import sectionTitle from "../../../components/common/sectionTitle.vue";
    import IntegralStyleOne from "../../../components/mobile/point/integralStyleOne.vue";
    export default {
        name: "integralShop",
        components: { IntegralStyleOne, sectionTitle },
        data() {
            return {
                tableData: {},
                bannerList: [],
                prodList: []
            };
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('IntegralShopIndex', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'home'});
                    });
                });
            },
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    @import "../../../assets/css/common/lib";
    .integral-shop {
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        .el-carousel__item a{
            line-height: 180px;
        }
        .banner {
            .banner-img {
                width: 100%;
                height: 100%;
                .img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    background-size: cover !important;
                }
            }
            .banner-bottom {
                text-align: center;
                display: flex;
                justify-content: space-around;
                .item {
                    display: inline-block;
                    background: #fefefe;
                    width: calc(100%/3);
                    padding: 10px 0;
                    transition: all 0.3s;
                    -moz-transition: all 0.3s;
                    -webkit-transition: all 0.3s;
                    -o-transition: all 0.3s;
                    border-right: 1px solid #eee;
                    &:last-child{
                        border-right: 0;
                    }
                    @include diy-animation-show;
                    i,
                    span {
                        display: block;
                    }
                    &:hover {
                        @include diy-animation-show;
                        box-shadow: 0px 0px 20px #ccc;
                    }
                    .bbx-font {
                        font-size: 20px;
                    }
                    .span {
                        display: inline-block;
                        line-height: 18px;
                        vertical-align: text-bottom;
                        padding-left: 5px;
                    }
                    &:first-child {
                        i {
                            color: #b092e6;
                        }
                    }
                    &:nth-child(2) {
                        i {
                            color: #68dbbf;
                        }
                    }
                    &:last-child {
                        margin-right: 0;
                        i {
                            color: #ff9966;
                        }
                    }
                }
            }
        }
        .title-all {
            height: 40px;
            line-height: 40px;
            background: #fff;
            margin-top: 10px;
            padding: 0 10px;
            .left {
                float: left;
                font-weight: 600;
                height: 40px;
                line-height: 40px;
            }
            .right {
                float: right;
                height: 40px;
                line-height: 40px;
                font-size: 16px;
                cursor: pointer;
            }
        }
        .tips{
            background: #fff;
            text-align: center;
            color: #999;
            height: 50px;
            line-height: 50px;
        }
        .content-item {
            position: relative;
            background: #fff;
            padding: 10px;
            .content-ul {
                box-sizing: border-box;
                .content-list{
                    padding-bottom: 10px;
                    border-bottom: 1px solid #eee;
                    margin-bottom: 10px;
                    &:last-child{
                        margin-bottom: 0;
                        padding-bottom: 0;
                        border-bottom: 0;
                    }
                }

            }
            &:last-child{
                margin-bottom: 10px;
            }
        }

    }
</style>

<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>